import './MyLabel.css';

export interface MyLabelProps {
	/**
	 * Texto por defecto
	 */
	label: string;
	/**
	 * TamaÃ±o por defecto del texto
	 */
	size: 'normal' | 'h1' | 'h2' | 'h3';

	/**
	 * Mayusculas
	 */

	allCaps: boolean;

	/**
	 * Color
	 */

	color: 'primary' | 'secondary' | 'tertiary';
}

export const MyLabel = ({
	label = 'No Label',
	allCaps = false,
	color,
	size = 'normal',
}: MyLabelProps) => {
	return (
		<span className={`${size} text-${color}`}>
			{allCaps === true ? label.toUpperCase() : label}
		</span>
	);
};
